@import "./base.scss";

.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background: #0b134f;
  display: grid;
  place-items: center;
  font-family: "Courier New", Courier, monospace;
  display: none;
  &__box {
    position: relative;
    width: 500px;
    height: 200px;
    border: 3px solid $green-color;
    border-top: 1px solid $green-color;
    border-bottom: 1px solid $green-color;

    @media(max-width: 600px) {
      & {
        width: 90vw;
      }
    }

  }
  &__bar {
    width: 90%;
    height: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    background: #ccc;
    border-radius: 2px;

    &--inner {
      width: 0;
      height: 100%;
      background: $green-color;
      border-radius: 2px;
      animation: loading 2s linear infinite;
    }
  }

  &__text {
    position: relative;
    color: #fff;
    padding: 1rem;
    font-size: 20px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    @media(max-width: 355px) {
      & {
        font-size:17px;
      }
    }

    &--dot {
      width: 15px;
      height: 15px;
      margin: 0 3px;
      border-radius: 50%;
      background: #fff;
      animation: pulse 1s infinite;

      @keyframes pulse {
        from {
          opacity: 0;
          background: $green-color;
        }
        to {
          transform: scale(1.5);
          opacity: 1;
        }
      }
    }
    &--border {
      width: 85%;
      height: 1px;
      background: $dark-green-color;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__counter {
    position: absolute;
    top: 70%;
    left: 0%;
    color: #fff;
    font-size: 20px;
    font-weight: 700;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;

    &--number {
      color: $green-color;
    }
  }
  &__width_animation {
    animation: width_animation 1s forwards;
    @keyframes width_animation {
      0% {
        width: 85%;
      }
      100% {
        width: 280px;
      }
    }
  }
  &__svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 250px;
    opacity: 0;
    z-index: 2;
    
  }
}

